@mixin onDesktop-max-1240 {
  @media (max-width: 1240px) {
    @content;
  }
}

@mixin onDesktop-min-1240 {
  @media (min-width: 1240px) {
    @content;
  }
}

@mixin onTablet {
  @media (max-width: 1024px) {
    @content;
  }
}

@mixin onMobile {
  @media (max-width: 710px) {
    @content;
  }
}

@mixin onSmallScreen {
  @media (max-width: 300px) {
    @content;
  }
}
